<script>
setTimeout(() => {
  var mark = document.querySelector("#marker");
  var item = document.querySelectorAll("nav a");
  var itemi = document.querySelector("#i");
  mark.style.left = itemi.offsetLeft + "px";
  mark.style.width = itemi.offsetWidth + "px";
  console.log(mark);
  function indicador(e) {
    mark.style.left = e.offsetLeft + "px";
    mark.style.width = e.offsetWidth + "px";
  }

  item.forEach((link) => {
    link.addEventListener("mouseenter", (e) => {
      indicador(e.target);
    });
  });
}, 1000);
// @ is an alias to /src
import Header from "@/components/header/Header.vue";
