  methods:{
    focus(to) {
      window.location.hash = to;
      if( document.body.clientWidth<651){
         var header = document.getElementById('header');
      header.style.transform = header.style.transform === 'translateX(0%)' ? 'translateX(-100%)' : 'translateX(0%)';
      }

    },
    toggle(){
          var header = document.getElementById('header');
      header.style.transform = header.style.transform === 'translateX(0%)' ? 'translateX(-100%)' : 'translateX(0%)';
    }

  }
};
