<template>
  <div id="form">
    <transition name="slide-fade">
      <div class="form-container" v-if="active">
        <div class="form">
          <h1 class="contact-header">Cont√°ctate Conmigo</h1>
          <div class="form-control">
            <input
              type="text"
              v-model="name"
              name="name"
              autocomplete="off"
              required
            />
            <label for="name"
              ><span class="inputlabel"
                ><i class="required">*</i>Nombre</span
              ></label
            >
            <span v-if="tip.name" class="tooltip">{{ tip.name }}</span>
          </div>
          <div class="form-control">
            <input
              type="text"
              v-model="email"
              name="email"
              autocomplete="off"
              required
            />

            <label for="email"
              ><span class="inputlabel"
                ><i class="required">*</i>Correo</span
              ></label
            >
            <span v-if="tip.email" class="tooltip">{{ tip.email }}</span>
          </div>
          <div class="form-control">
            <input
              type="text"
              v-model="phone"
              name="phone"
              autocomplete="off"
              maxlength="20"
              required
            />

            <label for="phone"><span class="inputlabel">Telefono </span></label>
          </div>
          <div class="form-control">
            <textarea
              name="message"
              v-model="message"
              id=""
              cols="30"
              rows="6"
              required
            ></textarea>
            <label class="txt" for="name"
              ><span class="inputlabel"
                ><i class="required">*</i>Mensaje</span
              ></label
            >
            <span v-if="tip.message" class="tooltip">{{ tip.message }}</span>
          </div>
          <a
            id="btn"
            class="btn"
            @click="contactgmail"
          >
            <span> Enviar </span></a
          >
          <div class="wrap" v-if="isLoading">
            <div class="loading">
              <div class="bounceball"></div>
              <div class="text">Espera por favor...</div>
            </div>
          </div>
          <br />
          <div style="color: green" v-if="sent" class="text">
            Gracias por contactarte conmigo =D
          </div>
        </div>
      </div>
    </transition>
  </div>
</template>
    <script src="https://smtpjs.com/v3/smtp.js"></script>   

<script>
export default {
  data() {
    return {
      isLoading: false,
      sent: false,
      active: false,
      email: "",
      phone: "",
      name: "",
      message: "",
      ready: false,
      tip: {
        name: "",
        email: "",
        message: "",
      },
    };
  },
  methods: {
    toggle() {
      this.active = !this.active;
      if (this.active) {
        this.scroll();
      }
    },
    scroll() {
      setTimeout(() => {
        window.location.hash = "form";
      });
    },
    contactgmail() {
      this.ready = false;
      var emailRegex = /^(([^<>()[\]\.,;:\s@\"]+(\.[^<>()[\]\.,;:\s@\"]+)*)|(\".+\"))@(([^<>()[\]\.,;:\s@\"]+\.)+[^<>()[\]\.,;:\s@\"]{2,})$/i;
      var regexNombre = /^[a-zA-Z√ë√±√Å√°√â√©√ç√≠√ì√≥√ö√∫√ú√º\s]+$/;
      this.isLoading = true;
      this.sent = false;
      this.tip = {
        name: "",
        email: "",
        message: "",
      };
      if (!this.ready) {
        if (this.name == "") {
          this.tip.name = "este espacio es requerido";
        } else if (!regexNombre.test(this.name)) {
          this.tip.name = "dame tu nombre real";
        }
        if (this.email == "") {
          this.tip.email = "este espacio es requerido";
        } else if (!emailRegex.test(this.email)) {
          this.tip.email = "ingresa un correo v√°lido";
        }

        if (this.message == "") {
          this.tip.message = "este espacio es requerido";
        }

        if (
          this.tip.name == "" &&
          this.tip.email == "" &&
          this.tip.message == ""
        ) {
          this.ready = true;
          this.isLoading = true;
        } else {
          this.isLoading = false;
        }
      }

      if (this.ready) {
        Email.send({
          Host: "smtp.gmail.com",
          Username: "reyesjohan665@gmail.com",
          Password: "bcpvmwbvoxlwxtiw",
          To: "reyesjohan59@gmail.com",
          From: this.email,
          Subject: "Solicitud de Contacto" + this.email,
          Body: `${this.name} se quiere contactar contigo ${this.phone} ${this.message}`,
        })
          .then((message) => {
            this.isLoading = false;
            this.sent = true;
            this.ready = true;
            this.reset();
          })
          .catch((msg) => {
            console.log(msg);
          });
      }
    },
    reset() {
      this.name = "";
      this.email = "";
      this.phone = "";
      this.message = "";
    },
  },
};
</script>
<style lang="css">
.form-control {
  width: 100%;
  float: right;
  position: relative;
  direction: rtl;
}
.tooltip::before {
  content: "";
  position: absolute;
  right: 0px;
  top: auto;
  margin-top: -11px;
  width: 0;
  height: 0;
  border-bottom: 9px solid rgb(174, 0, 0);
  border-right: 9px solid transparent;
  z-index: 9999;

  border-left: 9px solid transparent;
}
.tooltip {
  display: table-cell;
  position: absolute;
  background: rgb(174, 0, 0);
  padding: 3px;
  z-index: 9999;
}
.dis {
  cursor: not-allowed !important;
  opacity: 0.65;
}
.btn {
  position: relative;
  display: inline-block;
  border:2px solid #fff;
  color: #fff;
  text-decoration: none;
  font-weight: 500;
  font-size: 17px;
  width: 100%;
  text-transform: uppercase;
  padding: 15px 30px;
  cursor: pointer;
  
 
}
.btn:before{
  content: '';
  position: absolute;
  top: 6px;
  left: -2px;
  width: calc(100% + 6px);
  height: calc(100% - 12px);
  background: #14263c;
  transition: 0.5s ease-in-out;
  transform: scaleY(0);
  transition-delay: 0.5s;
}
.btn:hover:before{
  transform: scaleY(1);
}

.btn:after{
  content: '';
  position: absolute;
  left: 6px;
  top: -2px;
  height: calc(100% + 4px);
  width:   calc(100% - 12px);
  background: #14263c;
  transition: 0.5s ease-in-out;
  transform: scaleX(0); 
}
.btn:hover:after{
  transform: scaleX(1);
}
.btn span{
  position: relative;
  z-index: 3;
}

.contact-header::after {
  content: "";
  position: absolute;
  background: rgb(174, 0, 0);
  margin-top: 45px;
  width: 100px;
  left: 0;
  right: 0;
  margin-left: auto;
  margin-right: auto;
  text-transform: uppercase;
  height: 3px;
}
.wrap {
  position: relative;
  left: 50%;
  bottom: -25px;
  transform: translate(-50%, -50%);
}
.text {
  color: #fff;
  display: inline-block;
  margin-left: 5px;
}
.bounceball {
  position: relative;
  display: inline-block;
  height: 35px;
  width: 15px;
}
.bounceball:before {
  position: absolute;
  content: "";
  display: block;
  top: 0;
  width: 15px;
  height: 15px;
  border-radius: 50%;
  background-color: #3abff4;
  transform-origin: 50%;
  animation: bounce 500ms alternate infinite ease;
}
@keyframes bounce {
  0% {
    top: 30px;
    height: 5px;
    border-radius: 60px 60px 20px 20px;
    transform: scaleX(2);
  }
  35% {
    height: 15px;
    border-radius: 50%;
    transform: scaleX(1);
  }
  100% {
    top: 0;
  }
}

.slide-fade-enter-active {
  transition: all 0.8s ease;
}
.slide-fade-leave-active {
  transition: all 0.8s cubic-bezier(1, 0.5, 0.8, 1);
}
.slide-fade-enter, .slide-fade-leave-to
/* .slide-fade-leave-active below version 2.1.8 */ {
  transform: translateX(-100%);
  opacity: 0;
}
.required {
  color: red;
}
.form-container {
  padding: 15px 0 10px 0;
  width: 60%;
  left: 0;
  right: 0;
  margin-left: auto;
  margin-right: auto;
  background: #14263c;
  color: white;
}
@media only screen and (max-width: 750px) {
  .form-container {
    width: 90%;
  }
}
.form {
  text-align: center !important;
  left: 0;
  right: 0;
  margin-left: auto;
  margin-right: auto;
  transition: 1s ease-in-out;
  width: 80%;
  text-align: initial;
  overflow: hidden;
}

.form label {
  color: #fff;
  float: left;
  width: 100%;
  position: relative;
  bottom: 2px;
}
.form label::after {
  left: 0;
  bottom: 8px;
  content: "";
  position: absolute;
  height: 100%;
  width: 100%;
  border-bottom: 3px solid rgb(20, 186, 237);
  transform: translateX(-100%);
  transition: 1s ease;
}
.form .txt::after {
  left: 0;
  bottom: 11px;
  content: "";
  position: absolute;
  height: 100%;
  width: 100%;
  border-bottom: 3px solid rgb(20, 186, 237);
  transform: translateX(-100%);
  transition: 1s ease;
}
.form textarea {
  width: 100%;
  min-width: 100%;
  min-height: 20%;
  max-height: 300px;
  text-decoration: none;
  outline: none;
  border: none;
  background: none;
  transition: 0.5s;
  font-size: 14px;
  color: #fff;
  border-bottom-width: 0%;
  margin-bottom: 10px;
  border-bottom: 2px solid #fff;
}
.form input {
  z-index: 999;
  padding-top: 20px;
  width: 100%;
  line-height: 2em;
  text-decoration: none;
  outline: none;
  border: none;
  background: none;
  transition: 0.5s;
  font-size: 14px;
  color: #fff;
  border-bottom-width: 0%;
  margin-bottom: 10px;
  border-bottom: 2px solid #fff;
}
.inputlabel {
  position: absolute;
  bottom: 15px;
  left: 0px;
  transition: all 0.3s ease;
}
.txt .inputlabel {
  position: absolute;
  bottom: 20px;
  left: 0px;
  transition: all 0.3s ease;
}
.form input:focus + label .inputlabel,
.form input:valid + label .inputlabel {
  transform: translateY(-150%);
  font-size: 14px;
  color: rgb(20, 186, 237);
}
.form textarea:focus + .txt .inputlabel,
.form textarea:valid + .txt .inputlabel {
  transform: translateY(-600%);
  font-size: 14px;
  color: rgb(20, 186, 237);
}
.form input:focus + label::after,
.form input:valid + label::after {
  transform: translateX(0%);
}
.form textarea:focus + label::after,
.form textarea:valid + label::after {
  transform: translateX(0%);
}
.form textarea {
  width: 100%;
}
textarea,
input {
  outline: none;
  direction: initial;
}
textarea {
  margin-top: 10px;
  resize: none;
}
</style>