<template>
  <div id="app">
    <div id="progressBar" ref="progress"></div>
    <div id="scrollPath"></div>
    <!-- <div id="nav">
      <router-link to="/">Home</router-link> |
      <router-link to="/about">About</router-link>
    </div> -->
    <router-view />
  </div>
</template>

<script>
export default {
  data() {
    return {
      progress: "",
      totalHeight: 1,
      progressHeight: "",
    };
  },
  created() {},
  mounted() {
    this.redirect();

    window.addEventListener("scroll", this.scroll);
  },
  destroy() {
    window.removeEventListener("scroll", this.scroll);
  },
  methods: {
    redirect() {
      if (this.$router.currentRoute.path != '/') {
        window.location.replace("/");
      }
    },
    getHeight() {},
    scroll() {
      this.progress = document.getElementById("progressBar");
      this.totalHeight = document.body.scrollHeight - window.innerHeight;
      this.progressHeight = (window.pageYOffset / this.totalHeight) * 100;
      this.progress.style.height = this.progressHeight + "%";
      this.progress.classList.add("change");
      setTimeout(() => {
        this.progress.classList.remove("change");
      }, 1400);
    },
  },
};
</script>

