}
body .logo-icon path {
  fill: transparent;
  stroke-width: 70;
  stroke: #31e8ff;
}
body .logo-icon.logoJ path {
  stroke-dasharray: 18900;
  stroke-dashoffset: 0;
}

.header:hover .logo-icon.logoJ path {
  animation: animate-logo 1.6s linear forwards;
}
