}

.header {
  width: 100%;
  height: 80vh;
  position: relative;
  display: grid;
  grid-template-rows: max-content 1fr;
  grid-template-columns: 1fr max-content;

  &__logo {
    fill: white;
    width: 3rem;
    padding: 1rem;
    cursor: pointer;
  }

  &__bg {
    width: 100%;
    height: 100%;
    background: #232323;
    filter: brightness(50%);
    clip-path: polygon(0 0, 100% 0, 100% 80%, 0 100%);
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
  }

  &__title {
    grid-area: 2 / 1 / 2 / 3;
    align-self: center;
    justify-self: center;
    color: white;
    text-transform: uppercase;
    font-size: 4rem;
    text-shadow: 0px 2px 4px rgba(0, 0, 0, 0.4);
    margin-top: -5rem;
  }

  &__log {
    display: flex;
    align-items: center;
    grid-area: 1 / 2 / 1 / 3;
    padding: 1rem 2rem;
    color: white;
    cursor: pointer;
    text-shadow: 0px 2px 4px rgba(0, 0, 0, 0.4);

    &--icon {
      fill: currentColor;
      margin-right: 0.5rem;
      filter: drop-shadow(0px 2px 4px rgba(0, 0, 0, 0.5));
    }

    &--text {
      text-transform: capitalize;
    }
  }
}

.about {
